/* Home Management Btn */
.m-btn {
    @extend .btn;
    &.s-btn {
        @extend .management_c2-bg;
        &:hover {
            background: $management_c1;
        }
    }
    &.border-btn {
        border-color: $management_c2;
        color: $black;
        &:hover {
            background: $management_c2;
            color: $white;
        }
        &.style--two {
            @extend .management_c1-bo;
            @extend .management_c1;
            &:hover {
                @extend .management_c1-bg;
                color: $white;
            }
        }
    }
