/* Header Button */
.header-btn {
    a {
        &:not(:last-child) {
            margin-right: 36px;
        }
    }
    &.book {
        a {
            display: inline-block;
            &:not(:last-child) {
                margin-right: 50px;
            }
        }
    }
    .btn-crm {
        &.style--two, &.style--three {
            padding: 15px 33px;
        }
    }
    .btn-crs {
        &.style--three {
            padding: 15px 33px;
        }
    }
    .link-btn {
        color: $black;
        @extend .hind;
        font-size: 18px;
        font-weight: $medium;
        position: relative;
        text-transform: capitalize;
        &:hover {
            @extend .c1;
        }

        &.style--two {
            color: $black;
            &:hover {
                @extend .crm_c2;
            }
        }
        &.style--three {
            @extend .crs_c2;
            &:before {
                position: absolute;
                content: '';
                @extend .crs_c2-bg;
                left: 0;
                bottom: 0;
                width: 100%;
                height: 1px;
                transition: .3s;
            }
            &:after {
                position: absolute;
                content: '';
                @extend .crs_c2-bg;
                left: 0;
                bottom: 0;
                width: 0;
                height: 1px;
                transition: .3s;
                transition-delay: .3s;
            }
            &:hover {
                &:before {
                    left: auto;
                    right: 0;
                    width: 0;
                }
                &:after {
                    width: 100%;
                }
            }
        }
    }
    &.management {
        .link-btn {
            position: relative;
            line-height: 1;
            &:not(:last-child) {
                padding-right: 20px;
                margin-right: 16px;
                &:hover {
                    @extend .management_c2;
                }
                &:after {
                    position: absolute;
                    content: '';
                    top: 0;
                    right: -1px;
                    width: 1.5px;
                    height: 100%;
                    background: $black;
                }
            }
            &:last-child {
                @extend .management_c1;
                &:hover {
                    @extend .management_c2;
                }
            }
        }
    }
    &.pos {
        .link-btn {
            position: relative;
            line-height: 1;
            &:not(:last-child) {
                padding-right: 20px;
                margin-right: 16px;
                @extend .analytic_c1;
                &:hover {
                    @extend .analytic_c2;
                }
                &:after {
                    position: absolute;
                    content: '';
                    top: 0;
                    right: -1px;
                    width: 1.5px;
                    height: 100%;
                    background-color: #8A8A8A;
                }
            }
            &:last-child {
                @extend .analytic_c2;
                &:hover {
                    @extend .analytic_c1;
                }
            }
        }
    }
}

/* Banner Btn Group */
.banner-btn-group {
    a {
        display: inline-block;
        &:not(:last-child) {
            margin-right: 26px;
        }
        @include medium {
            &:not(:last-child) {
                margin-right: 20px;
            }
        }
        @include mobileMd {
            &:not(:last-child) {
                margin-right: 10px;
                margin-bottom: 10px;
            } 
        }
    }
}

/* CTA Button Group */
.cta-btn-group {
    @include mobileMd {
        display: inline-flex;
        flex-direction: column;
    }
    a {
        &:not(:last-child) {
            margin-right: 20px;
        }
        @include mobileMd {
            &:not(:last-child) {
                margin-right: 0px;
                margin-bottom: 10px;
            }
        }
    }
    .btn-crs.plug {
        padding: 20px 35px;
    }
}

/* Ebook-btn */
.e-btn {
    @extend .btn-pay;
    top: 6px;
    left: -6px;
    &:after {
        position: absolute;
        left: 6px;
        top: -6px;
        width: 100%;
        height: 100%;
        background: transparent;
        border: 1px solid $white;
        content: "";
        z-index: -1;
        @extend .trans3;
    }
    &:hover {
        background: $email_c2;
        top: 0;
        left: 0;
        &:after {
            left: 0px;
            top: 0px;
        }
    }
    &.s-btn {
        background: $email_c1;
        color: $white;
        &:after {
            border-color: $black;
        }
        &:hover {
            background: $email_c1;
        }
    }
    &.inner {
        &:after {
            border: 1px solid $black;
        }

    }
}

/* Line Btn */
.line-btn {
    @extend .email_c1;
    position: relative;
    padding: 0;
    text-transform: capitalize;
    font-size: 18px;
    font-weight: $semi-bold;
    @extend .hind;
    &:before {
        position: absolute;
        content: '';
        @extend .email_c1-bg;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 1px;
        transition: .3s;
    }
    &:after {
        position: absolute;
        content: '';
        @extend .email_c1-bg;
        left: 0;
        bottom: 0;
        width: 0;
        height: 1px;
        transition: .3s;
        transition-delay: .3s;
    }
    &:hover {
        @extend .email_c1;
        &:before {
            left: auto;
            right: 0;
            width: 0;
        }
        &:after {
            width: 100%;
        }
    }
    &.analytic {
        @extend .analytic_c2;
        &:before {
            @extend .analytic_c2-bg;
        }
        &:after {
            @extend .analytic_c2-bg;
        }
    }
    &.app {
        @extend .app_c2;
        &:before {
            @extend .app_c2-bg;
        }
        &:after {
            @extend .app_c2-bg;
        }
    }
    &.proto {
        @extend .proto_c2;
        &:before {
            @extend .proto_c2-bg;
        }
        &:after {
            @extend .proto_c2-bg;
        }
    }
    &.job {
        @extend .email_c2;
        &:before {
            display: none;
        }
        &:after {
            width: 60%;
            @extend .email_c2-bg;
        }
        &:hover:after {
            width: 100%;
        }
    }
    &.job-header {
        @extend .email_c2;
        &:before {
            @extend .email_c2-bg;
        }
        &:after {
            @extend .email_c2-bg;
        }
    }
    &.reading-btn {
        color: $black;
        &:before {
            background-color: $black;
        }
        &:after {
            background-color: $black;
        }
    } 

}

/* Download Button */
.download-btn {
    margin-right: 30px;
    display: inline-block;
    padding: 16px 30px !important;
    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
        width: 195px;
    }
    @include mobileMd {
        margin-right: 0;
        margin-bottom: 20px;
    }
    .icon {
        margin-right: 10px;
    }
    .content {
        p {
            line-height: 1;
            color: $white;
            margin-bottom: 5px !important;
            font-size: 12px;
            font-weight: $regular;
        }
        h5 {
            color: $white;
            font-weight: $semi-bold;
        }
    }
}
.download-btn2 {
    display: inline-block;
    padding: 16px 30px !important;
    background: $white !important;
    @extend .box-shadow;
    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
        width: 180px;
    }
    .icon {
        margin-right: 10px;
        .svg {
            path {
                @extend .trans2;
            }
        }
    }
    .content {
        p {
            line-height: 1;
            color: $black;
            margin-bottom: 5px !important;
            font-size: 12px;
            font-weight: $regular;
            @extend .trans2;
        }
        h5 {
            color: $black;
            font-weight: $semi-bold;
            @extend .trans2;
        }
    }
    &:hover {
        .icon {
            margin-right: 10px;
            .svg {
                path {
                    fill: $white;
                }
            }
        }
        .content {
            p {
                color: $white;
            }
            h5 {
                color: $white;
            }
        }
    }
}

/* CTA Button Group Responsive */
.cta-btn-group {
    &.proto {
        @include mobileLg {
            a {
                &:not(:last-child) {
                    margin-right: 0;
                    margin-bottom: 14px;
                }
            }
        }
    }
}