/* ************************
   04.6: Feature
   ********************* */
.feature-content {
    h2 {
        //@extend .crm_c1;
        margin-bottom: 10px;
    }
    @include tab {
        margin-bottom: 60px;
    }

    &.style--two {
        &.pay {
            padding: 140px 0 130px;
            padding-right: 100px;
            @include medium {
                padding-right: 60px;
            }
            @include tab {
                padding: 80px 0 70px;
                padding-right: 0;
                margin-bottom: 60px;
            }
            
        }
        &.pay2 {
            padding: 140px 0 90px;
            padding-left: 140px;
            @include medium {
                padding-left: 100px;
            }
            @include tab {
                padding-left: 0px;
                padding: 80px 0 30px;
            }
        }
    }
    &.plug {
        margin-bottom: 0;
        .section-title {
            margin-bottom: 40px;
            h2 {
                margin-bottom: 10px;
                font-weight: $semi-bold;
            }
        }
        &.style--two {
            .section-title {
                margin-bottom: 10px;
            }
        }
        .content {
            p {
                margin-bottom: 14px;
            }
            p.mb-30 {
                margin-bottom: 30px;
            }
        }
    }
    &.management {
        .section-title {
            margin-bottom: 30px;
            h2 {
                font-weight: $semi-bold;
                margin-bottom: 10px;
                line-height: 1.2;
                @include customMq(1200px, 1500px) {
                    br {
                        display: none;
                    }
                }
            }
            p {
                margin: 0;
            }
        }
        h2 {
            @extend .management_c1;
        }
    }
    &.pos {
        position: relative;
        padding: 60px 75px;
        background-color: #FFFCF2;
        margin-left: -30px;
        @include customMq(1200px, 1500px) {
            margin-left: 0;
            padding: 50px;
        }
        @include medium {
            margin-left: 0;
            padding: 50px;
            .list-check.pos {
                li p {
                    font-size: 18px;
                }
            }
        }
        @include mobileMd {
            padding: 50px 15px;
        }
    }
    &.app {
        padding: 0 50px;
        @include tab {
            padding: 0;
        }
        h2 {
           font-size: 50px;
           font-weight: $semi-bold;
           line-height: 1.3;
           color: $black;
           margin-bottom: 20px;
           @include mobileMd {
               font-size: 40px;
           }
        }
        p {
            margin-bottom: 30px;
        }
        &.style--two {
            padding: 0;
            padding-right: 120px;
            margin: 0;
            @include medium {
                padding-right: 50px;
            }
            @include tab {
                padding: 0;
                padding-bottom: 20px;
            }
        }
    }
    &.app2 {
        padding-left: 120px;
        margin: 0;
        @include medium {
            padding-left: 40px;
        }
        @include tab {
            padding-left: 0;
        }
    }
    &.l-app {
        h2 {
            font-size: 50px;
            color: $black;
            line-height: 1.2;
            margin-bottom: 20px;
            @include tab {
                margin-bottom: 50px;
            }
            @include medium {
                br {
                    display: none;
                }
            }
            @include mobileMd {
                font-size: 40px;
            }
        }
        p{
            margin-bottom: 30px;
            font-weight: $regular;
        }
    }
    &.proto {
        h2 {
            @extend .proto_c1;
            margin-bottom: 20px;
            line-height: 1.25;
            @include mobileLg {
                font-size: 36px;
            }
        }
        p {
            margin-bottom: 30px;
        }
    }
}

.feature-img {
    display: flex;
    align-items: center;
    position: relative;
    z-index: 1;
    .f-bg {
        position: relative;
        left: auto;
        right: -250px;
        width: 118%;
        top: 0px;
        z-index: -1;
        &.crm {
            position: absolute;
            top: -70px;
            @include customMq(1200px, 1602px) {
                top: 0;
            }
            @include medium {
                top: -100px;
            }
            @include tab {
                top: 0px;
                left: auto;
                right: -15px;
                width: 100%;
            }
       }

        @include customMq(1200px, 1602px) {
            left: 140px;
            right: auto;
            width: 100%;
        }
        @include medium {
            left: 0px;
        }
    }
    .f-img {
        position: absolute;
        left: auto;
        right: -50px;
        &.crm {
             position: relative;
        }

        @include medium {
            right: 0px;
            padding: 0 15px;
        }
    }
    .mobile-bg {
        position: absolute;
        z-index: -1;
        animation: spin 60s linear .3s infinite forwards;
        @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
            bottom: 80px;
        }
    }
    .pay-f2, .host-f2 {
        @include customMq(992px, 1500px) {
            padding-right: 30px;
        }
    }
    &.style--two {
        .f-bg {
            left: -270px;
            @include medium {
                left: -100px;
            }
        }
        .f-img {
            right: 0px;
            @include medium {
                right: 0px;
            }
        }
    }
    &.style--three {
        .f-bg {
            left: 290px;
            @include medium {
                left: 100px;
            }
            @include tab {
                right: 0;
            }
        }
        .f-img {
            right: -90px;
            @include customMq(1200px, 1602px) {
                right: -60px;
            }
            @include medium {
                right: 0px;
            }
            @include tab {
                right: 0;
            }
        }
    }
    &.plug {
        @include tab {
            margin-bottom: 60px;
        }
    }
    &.analytic {
        @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
            .feature-map3 {
                width: 100%;
            }
        }
    }
    &.app {
        img {
            margin-left: -150px;
            max-width: none;
            @include customMq(991px, 1500px) {
                max-width: 140%;
            }
            @include tab {
                max-width: 100%;
                margin-left: 0;
                margin-bottom: 30px;
            }
        }
    }
    &.app2 {
        img {
            margin-left: -15px;
            max-width: none;
            @include customMq(991px, 1500px) {
                max-width: 120%;
            }
            @include tab {
                max-width: 100%;
                margin-left: 0;
                margin-bottom: 40px;
            }
        }
    }
    &.l-app {
        margin-left: 60px;
        @include tab {
            margin-left: 0;
        }
        img {
            @extend .box-shadow;
        }
        &.two {
            margin: 0;
            margin-right: 60px;
            @include tab {
                margin: 0;
            }
        }
    }
    &.proto {
        padding-left: 20px;
        @include tab {
            padding-left: 0px;
        }
        &.left {
            padding-left: 0;
        }
        .proto-img {
            max-width: none;
            @include tab {
                max-width: 100%;
            }
            &.left {
                left: -270px;
                position: relative;
                @include customMq(1200px, 1500px) {
                    left: -360px;
                }
                @include medium {
                    left: -400px;
                }
                @include tab {
                    left: 0px;
                }
            }
        }
    }
}

//Only For Mozila
@-moz-document url-prefix() {
    .feature-img {
        &.proto {
            .proto-img {
                max-width: none;
                min-width: 808px;
                @include tab {
                    max-width: 100%;
                    min-width: auto;
                }
            }
        }
    }
}



