        left: auto;
        right: -1000%;
    }
}

/* Book Page Feature*/
.single-feature-wrap:nth-child(even) {
    margin-top: 50px;
    @include mobileLg {
        margin-top: 0;
    }
}
.single-feature-wrap {
    @include mobileLg {
        &:not(:last-child) {
            margin-bottom: 30px;
        }
    }
}
.single-feature {
    padding: 50px 40px 44px;
    background: #FFFCF1;
    border-top-left-radius: 50px;
    @include medium {
        padding: 40px 20px 34px;
    }
    .feature-icon {
        margin-bottom: 35px;
    }
    .feature-content {
        margin-bottom: 0;
        h3 {
            font-size: 26px;
            font-weight: $medium;
            margin-bottom: 6px;
            color: $black;
            @include mobileMd {
                font-size: 24px;
            }
        }
        p {
            font-size: 20px;
            @include mobileMd {
                font-size: 16px;
            }
        }
    }
    &.two {
        background: #F8FBFF;
        border-top-left-radius: 0px;
        border-bottom-right-radius: 50px;
    }
    &.three {
        background: #F9FFF1;
    }
    &.four {
        
        background: #FFF3F1;
        border-top-left-radius: 0px;
        border-top-right-radius: 50px;
    }
    &.app {
        text-align: center;
        background-color: transparent;
        padding: 0;
        margin-bottom: 40px;
        .feature-icon {
            margin-bottom: 45px;
            @include mobileSm {
                margin-bottom: 30px;
            }
        }
        .feature-content {
            h3 {
                font-weight: $semi-bold;
                font-size: 30px;
                margin-bottom: 20px;
            }
        }
    }
    &.app2 {
        text-align: center;
        background-color: transparent;
        padding: 0;
        margin-bottom: 40px;
        .feature-icon {
            margin-bottom: 30px;
            .circle {
                position: relative;
                width: 172px;
                height: 172px;
                img, .svg {
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);
                }
                &.left {
                    &:after {
                        position: absolute;
                        content: "";
                        z-index: -1;
                        height: 100%;
                        width: 50%;
                        background: #EBF6FF;
                        border-bottom-left-radius: 172px;
                        border-top-left-radius: 172px;
                        left: 0;
                        top: 0;
                    }
                }
                &.bottom {
                    &:after {
                        position: absolute;
                        content: "";
                        z-index: -1;
                        height: 50%;
                        width: 100%;
                        background: #E2FFF9;
                        border-bottom-left-radius: 172px;
                        border-bottom-right-radius: 172px;
                        left: 0;
                        top: auto;
                        bottom: 0;
                    }
                }
                &.right {
                    &:after {
                        position: absolute;
                        content: "";
                        z-index: -1;
                        height: 100%;
                        width: 50%;
                        background: #FFF6F3;
                        border-bottom-right-radius: 172px;
                        border-top-right-radius: 172px;
                        left: auto;
                        right:0;
                        top: 0;
                    }
                }
            
            }
        }
        .feature-content {
            h3 {
                font-weight: $semi-bold;
                font-size: 30px;
                margin-bottom: 20px;
            }
        }
    }
    &.style--two {
        background: transparent;
        border-radius: 0;
        text-align: center;
    }
}

/* Ebook Feature */
.feature-box {
    position: relative;
    @include tab {
        margin-bottom: 80px;
    }

    &:after, .ebook-reveal {
        background: #F8FBFF;
        position: absolute;
        width: 1116%;
        height: 100%;
        content: "";
        left: -1000%;
        top: 0;
        z-index: -1;
        border-top-right-radius: 150px;
    }
    &:before {
        position: absolute;
        content: "01.";
        left: 0;
        top: -50px;
        font-size: 150px;
        @extend .hind;
        @extend .crs_c2;
        font-weight: $semi-bold;
        line-height: 1;
        @include mobileMd {
            font-size: 100px;
            top: -30px;
        }
    }

    .img {
        margin-bottom: 60px;
    }
    h2 {
        font-weight: $semi-bold;
        @extend .book_c1;
        margin-bottom: 18px;
        @include mobileMd {
            font-size: 30px;
        }
    }

    &.two {
        margin-top: 140px;
        @include tab {
            margin-top: 0;
        }
        &:after {
            width: 1116%;
            height: 100%;
            left: auto;
            right: -1000%;
            border-top-right-radius: 0px;
            border-top-left-radius: 150px;
        }
        
        &:before {
            content: "02.";
            @extend .crs_c2;
        }
        h2 {
            @extend .crs_c2;
        }
        &.four {
            &:after {
                border-radius: 0 0 0 150px;
            }
            &:before {
                content: "04.";
            }
            h2 {
                @extend .book_c1;
            }
        }
    }
    &.three {
        &:after {
            border-radius: 0 0 150px 0;
        }
        &:before {
            content: "03.";
            @extend .crs_c2;
        }
        h2 {
            @extend .crs_c2;
        }
    }
}

