/* Works Slider */ 
.work-slider {
    .work-slide-text {
        h2 {
            color: $black;
            font-size: 32px;
            font-weight: $semi-bold;
            margin-bottom: 20px;
        }
        p {
            font-size: 22px;
        }
    }
    .work-slide-image {
        @include tab {
            margin-bottom: 60px;
        }
    }
}
.work-slider-dots {
    margin-top: -5px;
    margin-bottom: 55px;
    padding: 0px 40px;
}
@media only screen and (max-width: 991px) {
    .work-slider-dots {
        display: none !important;
    }
}
.work-slider-dots .owl-stage {
    display: flex;
    align-items: flex-end;
}
.work-slider-dots .owl-item {
    text-align: center;
}
.work-slider-dots .active .dots-count {
    padding-bottom: 17px;
    position: relative;
    font-size: 26px;
    line-height: 1.2;
    font-weight: $medium;
    color: #BCBCBC;
    @extend .hind;
    cursor: pointer;
    display: inline-block;
    @include medium {
        font-size: 22px;
    }
}
.work-slider-dots .active .dots-count .process-bar {
    position: absolute;
    bottom: 0px;
    left: 0px;
    width: 100%;
    height: 2px;
    background-color: transparent;
}
.work-slider-dots .active .dots-count .process-bar-active {
    position: absolute;
    bottom: 0px;
    left: 0px;
    width: 0;
    height: 2px;
    background-color: #232323;
}
.work-slider-dots .active.current .dots-count {
    color: #232323;
}
.work-slider-dots .active.current .dots-count .process-bar {
    background-color: #dbdbdb;
}
.work-slider-dots .active.current .dots-count .process-bar-active {
    transition: width 4.9s;
    width: 100%;
}

