            margin: 0;
        }
        &:hover {
            .feature-icon {
                animation: fadeInUp10 .5s ease-in;
            }
        }
    }
}

/* Trapezium */
.trapezium-section {
    @include mobileLg {
        margin-bottom: 50px;
    }
}
.trapezium {
    position: relative;
    padding: 80px 20px;
    text-align: center;
    margin-bottom: 100px;
    @include mobileLg {
        margin-bottom: 50px;
    }
    &:before {
        content: "";
        position: absolute;
        background-color: #F1F5FE;
        width: 114%;
        @extend .radius-20;
        top: 0;
        bottom: 0;
        left: -1%;
        right: 0;
        z-index: -1;
        -webkit-transform: perspective(50em) rotateX(30deg);
        transform: perspective(800px) rotateY(30deg);
        @extend .trans3;
        @include customMq(992px, 1500px) {
            left: -4%;
        }
        @include mobileLg {
            left: 15px;
            width: 112%;
        }
        @include mobileMd {
            left: 0;
        }
    }
    &.two {
        &:before {
            left: auto;
            right: -1%;
            -webkit-transform: perspective(50em) rotateX(-30deg);
            transform: perspective(800px) rotateY(-30deg);
            @include medium {
                right: -3%;
            }
            @include mobileLg {
                right: 15px;
            }
            @include mobileMd {
                right: 0;
            }
        }
        &:hover {
            &:before {
                left: auto;
                right: 0;
            }
        }
    }
    &:hover {
        &:before {
            width: 100%;
            left: 0;
            -webkit-transform: perspective(50em) rotateX(0deg);
            transform: perspective(800px) rotateY(0deg);
        }
    }
    .feature-icon {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100px;
        height: 100px;
        background-color: $white;
        @extend .radius-20;
        margin: 0 auto 30px;
    }
    .feature-content {
        h3 {
            @extend .proto_c1;
            font-size: 26px;
            font-weight: $medium;
            margin-bottom: 10px;
        }
        p {
            font-size: 18px;
            max-width: 390px;
            margin-left: auto;
            margin-right: auto;
        }
    }
}

/* Prototype Availability */
.tab-btn.proto {
    margin-bottom: 40px;
    li {
        display: inline-block;
        @extend .hind;
        font-size: 26px;
        font-weight: $medium;
        cursor: pointer;
        position: relative;
        user-select: none;
        color: #BCBCBC;
        @include mobileXs {
            font-size: 24px;
        }
        &:not(:last-child) {
            margin-right: 65px;
            @include mobileLg {
                margin-right: 30px;
            }
            @include mobileMd {
                margin-bottom: 10px;
            }

        }
        &:after {
            position: absolute;
            content: "";
            @extend .proto_c2-bg;
            width: 0%;
            height: 2px;
            left: 50%;
            bottom: 0;
            transition-delay: .1s;
            @extend .trans3;
        }
        &.active {
            @extend .proto_c1;
            &:after {
                width: 100%;
                left: 0;
            }
        }
    }
}
.tab-content.proto {
    img {
        margin-bottom: 30px;
        &:not(:last-child) {
            margin-right: 55px;
            @include medium {
                margin-right: 30px;
            }
        }
        @include mobileSm {
            &:nth-child(even) {
                margin-right: 0;
            }
        }
    }
    div.active {
        animation: fadeInUp10 .4s linear 0s forwards;
    }
}