/* ************************
   02.1: Common Classes
   ********************* */
/* Transitions */
.trans2 {
    @include transition(.2s, linear);
}

.trans3 {
    @include transition(.3s, linear);
}

.trans4 {
    @include transition(.4s, linear);
}

.trans5 {
    @include transition(.5s, linear);
}

/* Font Family */
h1,
h2,
h3,
h4,
h5,
h6,
.hind {
    font-family: $hind;
}

body,
.ubuntu {
    font-family: $ubuntu;
}

/* Text White */
.text-white {
    *,
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        color: $white;
    }
} 

/* Border Radius */
.radius-5 {
    border-radius: 5px;
}

.radius-10 {
    border-radius: 10px;
}

.radius-20 {
    border-radius: 20px;
}

.radius-30 {
    border-radius: 30px;
}

.radius-40 {
    border-radius: 40px;
}

.radius-50 {
    border-radius: 50px;
}

.radius-60 {
    border-radius: 60px;
}

.radius {
    border-radius: 50%;
}

/* Box Shadow */
.box-shadow {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.06);
}

.no-shadow {
    box-shadow: none;
}

/* List Check */
.list-check {
    @extend .list-unstyled;
    li {
        font-size: 20px;
        padding-left: 35px;
        position: relative;
        font-weight: $medium;
        &:not(:last-child) {
            margin-bottom: 20px;
        }
        &:after {
            position: absolute;
            @include fa('\f00c');
            line-height: inherit;
            @extend .crm_c2;
            left: 0px;
            top: -2px;
        }
    }
    &.style--two {
        margin-top: 40px;
        margin-bottom: 44px;
        li {
            h3 {
                font-size: 26px;
                color: $black;
                font-weight: $medium;
                margin-bottom: 12px;
            }
            &:not(:last-child) {
                margin-bottom: 30px;
            }
            padding-left: 50px;
            font-weight: $medium;
            &:after {
                left: 0px;
                top: 0px;
                content: url('../img/feature/fb1.png');
            }
            &:nth-child(2) {
                &:after {
                    content: url('../img/feature/fb2.png');
                }
            }
        }
        &.pay {
            margin-top: 40px;
            margin-bottom: 0px;
            li {
                h3 {
                    font-size: 26px;
                    color: $black;
                    font-weight: $medium;
                    margin-bottom: 12px;
                }
                &:not(:last-child) {
                    margin-bottom: 30px;
                }
                padding-left: 50px;
                font-weight: $medium;
                &:after {
                    left: 0px;
                    top: 0px;
                    content: url('../img/icons/pay-a1.png');
                }
                &:nth-child(2) {
                    &:after {
                        content: url('../img/icons/pay-a2.png');
                    }
                }
                &:nth-child(3) {
                    &:after {
                        content: url('../img/icons/pay-a3.png');
                    }
                }
            }
        }
        
        &.pay2 {
            li {
                &:after {
                    left: 0px;
                    top: 0px;
                    content: url('../img/icons/pay-a4.png');
                }
                &:nth-child(2) {
                    &:after {
                        content: url('../img/icons/pay-a5.png');
                    }
                }
                &:nth-child(3) {
                    &:after {
                        content: url('../img/icons/pay-a6.png');
                    }
                }
            }
        }
    }
    &.style--three {
        margin-bottom: 44px;
        li {
            font-size: 16px;
            font-weight: $regular;
            color: $black;
            padding-left: 30px;
            &:not(:last-child) {
                margin-bottom: 5px;
            }
            &:after {
                top: -1px;
                @extend .crs_c1;
            }
        }
        &.price {
            margin-bottom: 32px;
        }
    }
    &.author-meta {
        li {
            font-size: 16px;
            font-weight: $light;
            &:not(:last-child) {
                margin-bottom: 5px;
            }
            &:after {
                content: url('../img/icons/a1.png');
                top: 4px;
            }
            &:nth-child(2) {
                &:after {
                    content: url('../img/icons/a2.png');
                }
            }
            &:nth-child(3) {
                &:after {
                    content: url('../img/icons/a3.png');
                }
            }
        }
    }
    &.cards, &.pos {
        li {
            &:not(:last-child) {
                margin-bottom: 30px;
            }
            h3 {
                color: $black;
                font-weight: $medium;
                margin-bottom: 10px;
            }
            &:after {
                @extend .pay_c2;
                top: -8px;
                font-size: 22px;
            }
        } 
    }
    &.email-check {
        display: flex;
        flex-wrap: wrap;
        li {
            flex: 0 0 50%;
            margin-bottom: 14px;
            text-transform: capitalize;
            font-size: 20px;
            font-weight: $light;
            @include mobileMd {
                flex: 0 0 100%;
            }
            &:after {
                @extend .email_c2;
            }
        }
    }
    &.host {
        li {
            margin-bottom: 18px;
            font-size: 18px;
            font-weight: $medium;
            @extend .hind;
            line-height: 1.3;
            padding-left: 30px;
            @include mobileMd {
                flex: 0 0 100%;
            }
            &:after {
                @extend .host_c2-bg;
                content: "";
                width: 10px;
                height: 10px;
                @extend .host_c1;
                @extend .radius;
                top: 4px;

            }
        }
    }
    &.pos {
        li {
            padding-left: 40px;
            &:after {
                @extend .analytic_c2;
            }
        }
    }
    &.job {
        li {
            font-weight: $light;
            &:not(:last-child) {
                margin-bottom: 15px;
            }
            &:after {
                content:"\f10c";
                @extend .email_c1;
                font-size: 12px;
                top: 8px;
            }
        }
    }
}
.list-uncheck {
    @extend .list-unstyled;
    li {
        &:not(:last-child) {
            margin-bottom: 35px;
        }
        h3 {
            font-size: 22px;
            font-weight: $medium;
            color: $black;
            margin-bottom: 8px;
        }
    }
    &.host {
        li {
            &:not(:last-child) {
                margin-bottom: 25px;
            }
            h3 {
                font-size: 24px;
            }
            p {
                font-size: 18px;
                max-width: 500px;
            }
        }
    }
}
.list-inline {
    li {
        display: inline-block;
    }
}

/* List Proses */
.list-proses {
    padding-left: 50px;
    @include medium {
        padding-left: 0;
    }
    li {
        padding: 40px;
        padding-bottom: 35px;
        @extend .radius-10;
        @extend .trans3;
        @include mobileMd {
            padding: 10px;
        }
        &:hover {
            background: #F1FAFF;
        }
        &:not(:last-child) {
            margin-bottom: 10px;
        }
        .img {
            margin-right: 30px;
        }
        .content {
            h3 {
                color: $black;
                font-size: 30px;
                font-weight: $medium;
                margin-bottom: 10px;
            }
            p {
                font-weight: $regular;
            }
        }
        &:nth-child(2) {
            &:hover {
                background: #E9FFFA;
            }
        }
        &:nth-child(3) {
            &:hover {
                background: #FFF4F3;
            }
        }
    }
}
.list-how-works {
    li {
        &:not(:last-child) {
            margin-bottom: 60px;
        }
        .img {
            margin-right: 50px;
            @include medium {
                margin-right: 40px;
            }
            @include mobileSm {
                margin: 0 auto 30px;
            }
        }
        @include mobileSm {
            flex-direction: column;
            text-align: center;
        }
        .content {
            h3 {
                color: $black;
                font-size: 30px;
                font-weight: $semi-bold;
                margin-bottom: 18px;
            }
            p {
                font-weight: $regular;
            }
        }
    }
}

/* Social Icons */
.social-icon {
    a {
        width: 40px;
        height: 40px;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        @extend .radius;
        color: $black;
        background: #f9f9f9;
        &:not(:last-child) {
            margin-right: 10px;
        }

        &:hover {
            @extend .c1-bg;
            color: $white;
        }
    }
    &.style--two {
        a {
            @extend .crm_c1-bg;
            color: $white;
            &:hover {
                @extend .crm_c2-bg;
                @extend .crm_c1;
            }
        }
    }
    &.analytic {
        a {
            @extend .analytic_c1-bg;
            color: $white;
            &:hover {
                @extend .analytic_c2-bg;
                color: $white;
            }
        }
    }
    &.pos {
        a {
            @extend .analytic_c1-bg;
            color: $white;
            &:hover {
                @extend .analytic_c2-bg;
                color: $white;
            }
        }
    }
    &.course {
        a {
            &:hover {
                @extend .crs_c1-bg;
                color: $white;
            }
        }
    }
    &.pay {
        a {
            background: $white;
            i {
                @extend .pay_c1;
                @extend .trans3;
            }
            &:hover {
                @extend .pay_c2-bg;
                i {
                    color: $white;
                }
            }
        }
    }
    &.plug {
        a {
            background: $white;
            i {
                color: $black;
                @extend .trans3;
            }
            &:hover {
                @extend .plug_c2-bg;
                i {
                    color: #4E2FA9;
                }
            }
        }
    }
    &.email {
        a {
            background: $white;
            i {
                color: $email_c1;
                @extend .trans3;
            }
            &:hover {
                @extend .email_c2-bg;
                i {
                    color: $white;
                }
            }
        }
    }
    &.management {
        a {
            background: $white;
            i {
                color: $management_c1;
                @extend .trans3;
            }
            &:hover {
                @extend .management_c2-bg;
                i {
                    color: $white;
                }
            }
        }
    }
    &.host {
        a {
            background: $white;
            i {
                color: $host_c3;
            }
            &:hover {
                @extend .host_c2-bg;
                i {
                    color: $white;
                }
            }
        }
    }
    &.app {
        a {
            background: $white;
            i {
                color: #2154FC;
            }
            &:hover {
                background-color: #2154FC;
                i {
                    color: $white;
                    animation: fadeInUp10 .3s linear .1s 1 backwards;
                }
            }
            &:nth-child(2) {
                i {
                    color: #3DB1FF;
                }
                &:hover {
                    background-color: #3DB1FF;
                    i {
                        color: $white;
                    }
                }
            }
            &:nth-child(3) {
                i {
                    color: #FF65A8;
                }
                &:hover {
                    background-color: #FF65A8;
                    i {
                        color: $white;
                    }
                }
            }
            &:nth-child(4) {
                i {
                    color: #238BDE;
                }
                &:hover {
                    background-color: #238BDE;
                    i {
                        color: $white;
                    }
                }
            }
        }
    }
    &.app2 {
        a {
            background: $white;
            @extend .box-shadow;
            i {
                color: $black;
            }
            &:hover {
                @extend .app2_c2-bg;
                i {
                    color: $white;
                }
            }
        }
    }
    &.proto {
        a {
            background: $white;
            i {
                color: $proto_c1;
                @extend .trans2;
            }
            &:hover {
                @extend .proto_c2-bg;
                i {
                    color: $white;
                }
            }
        }
    }
    &.about {
        span {
            display: block;
            margin-bottom: 10px;
        }
        a {
            @extend .email_c1-bg;
            color: $white;
            &:hover {
                @extend .email_c2-bg;
                color: $white;
            }
        }
    }
    &.style--three {
        a {
            background-color: #FFF6F1;
            color: $black;
            &:hover {
                background-color: #FFE8DC;
                color: $black;
            }
        }
    }
}
.footer-social-icons {
    @include tab {
        margin-bottom: 15px;
    }
    span {
        display: inline-block;
        margin-right: 5px;
        font-size: 18px;
    }
    a {
        display: inline-block;
        padding: 0 10px;
        font-size: 16px;
        &:hover {
            i {
                color: $crs_c2;
            }
        }
    }
}

/* Owl Dots */
.owl-carousel {
    .owl-dots {
        position: absolute;
        right: 50px;
        top: 50%;
        transform: translateY(-50%);
        width: 30px;
    }
    button.owl-dot {
        display: inline-block;
        height: 3px;
        width: 16px;
        @extend .c1-bg;
        @extend .trans3;
        &.active {
            width: 28px;
            @extend .c2-bg;
        }
    }
}

/* Owl Nav */
.owl-carousel {
    .owl-nav {
        button {
            position: absolute;
            right: 0;
            top: -80px;
            width: 50px;
            height: 50px;
            display: flex;
            justify-content: center;
            align-items: center;
            background: #C9FFF5 !important;
            @extend .radius-5;
            @extend .trans3;
            @include tab {
                top: -70px;
            }
            i {
                color: #5DD9C1;
                font-size: 30px;
            }
            &:hover {
               background: #5DD9C1 !important;
               i {
                    color: $white;
                }
            }
            &.owl-prev {
                right: 66px;
            }
        }
        @include tab {
            display: none;
        }
    }
    &.logo-carousel {
        .owl-nav {
            button {
                position: absolute;
                left:auto;
                right: -50px;
                top: calc(50% - 25px);
                width: 50px;
                height: 50px;
                background: $white !important;
                i {
                    color: $text-color;
                    font-size: 30px;
                }
                &:hover {
                   background: $white !important;
                   i {
                        color: $pay_c2;
                    }
                }
                &.owl-prev {
                    right: auto;
                    left: -50px;
                }
            }
            @include mobileLg {
                display: none;
            }
        }
        &.analytic {
            .owl-nav {
                button {
                    &:hover {
                       i {
                            color: $analytic_c2;
                        }
                    }
                }
            }
        }
    }
    &.book-review-slider,
    &.customer-review-slider {
        .owl-nav {
            button {
                position: absolute;
                left:auto;
                right: -90px;
                top: calc(50% - 25px);
                width: 50px;
                height: 50px;
                background: $white !important;
                @extend .radius;
                @include tab {
                    display: none;
                }
                i {
                    display: none;
                }
                &.owl-prev {
                    right: auto;
                    left: -90px;
                }
                .svg {
                    width: 22px;
                    g path {
                        @extend .trans3;
                        stroke: $black;
                    }
                }
    
                &:hover {
                    background: $book_c1 !important;
                    .svg {
                        g path {
                            stroke: $white;
                        }
                    }
                }
            }
            @include mobileLg {
                display: none;
            }
        }
    }
    &.customer-review-slider {
        .owl-nav {
            button {
                &:hover {
                    background: #4E2FA9 !important;
                }
            }
        }
    }
    &.student-review {
        .owl-nav {
            button {
                top: -110px;
                @include tab {
                    top: -86px;
                }
            }
        }
    }
    &.email-testimonial-slider,
    &.app-testimonial-slider {
        .owl-nav {
            button {
                top: auto;
                bottom: -50px;
                left: 0;
                background: transparent !important;
                padding: 0 !important;
                width: auto;
                height: auto;
                &.owl-next {
                    left: 53px;
                }
                .svg {
                    g {
                        fill: $text-color;
                        @extend .trans2;
                        path {
                            stroke: $text-color;
                            @extend .trans2;
                        }
                    }
                }
                &:hover {
                    .svg {
                        g {
                            fill: $email_c2;
                            path {
                                stroke: $email_c2;
                            }
                        }
                    }
                }
            }
        }
        &.host {
            .owl-nav {
                button {
                    &:hover {
                        .svg {
                            g {
                                fill: $host_c2;
                                path {
                                    stroke: $host_c2;
                                }
                            }
                        }
                    }
                }
            }
        }
        &.app2 {
            .owl-nav {
                button {
                    &:hover {
                        .svg {
                            g {
                                fill: $app2_c2;
                                path {
                                    stroke: $app2_c2;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    &.pos-feature-slider {
        .owl-nav {
            button {
                top: -140px;
                background: transparent !important;
                @include tab {
                    top: -70px;
                }
                i {
                    color: #707070;
                    font-size: 30px;
                }
                &:hover {
                   background: transparent !important;
                   i {
                        color: $analytic_c1;
                    }
                }
                &.owl-prev {
                    right: 66px;
                }
            }
            @include mobileLg {
                display: none;
            }
        }
    }
    &.app-testimonial-slider {
        .owl-nav {
            button {
                top: auto;
                bottom: 190px;
                left: -30px;
                @include tab {
                    bottom: 190px;
                }
                i {
                   display: none;
                }
                .svg {
                    width: 26px;
                }
                &:hover {
                    .svg g path {
                        stroke: $app_c1;
                    }
                }
                &.owl-next {
                    left: auto;
                    right: -30px;
                }
            }
            @include mobileLg {
                display: none;
            }
        }
    }
}

/* Theme Input Style */
.theme-input-style {
    border: 1px solid #eeeeee;
    height: 52px;
    width: 100%;
    padding: 0 20px;
    background-color: $white;
    font-size: 16px;
    @extend .trans3;
    margin-bottom: 20px;
    &:focus {
        background-color: #F8F8FF;
    }
}

textarea.theme-input-style {
    height: 200px;
    padding: 20px 20px;
}

/* Theme Input Group */
.theme-input-group {
    position: relative;
    width: 100%;
    input {
        width: 100%;
        height: 60px;
        border: none;
        padding-left: 25px;
        padding-right: 150px;
        color: $white;
        @extend .c1-bg;
        @extend .radius-50;
        margin: 0;
        @include mobileSm {
            padding-right: 130px;
            padding-left: 10px;
        }
    }
    button, button.submit-btn {
        position: absolute; 
        top: 0px;
        right: 0px;
        height: 100%;
        padding: 15px 30px;
        line-height: 1;
        font-weight: $semi-bold;
        color: $white;
        @extend .trans2;
        @extend .c2-bg;
        &:hover {
            @extend .c2-bg;
            @extend .c1;
        }
        @include mobileSm {
            font-size: 16px;
            padding: 20px;
        }
    }
    &.style--two {
        display: flex;
        margin-bottom: 12px;
        input {
            width: calc(100% - 170px);
            height: 50px;
            border: none;
            border-bottom: 1px solid $crm_c1;
            padding-left: 0px;
            padding-right: 0px;
            @extend .text-color;
            background: transparent;
            border-radius: 0;
            font-size: 18px;
            font-weight: $light;
            @include mobileSm {
                width: calc(100% - 100px);
            }
        }
        button {
            position: static;
            padding: 15px 30px;
            margin-left: 30px;
            line-height: 1;
            border-radius: 6px;
            font-weight: $semi-bold;
            @extend .trans2;
            @extend .crm_c2-bg;
            @extend .crm_c1;
            &:hover {
                @extend .crm_c1-bg;
                @extend .crm_c2;
            }
            @include mobileSm {
                margin-left: 5px;
                padding: 15px;
            }
        }
        &.analytic {
            input {
                border-bottom: 1px solid $white;
                color: $white;
                border-radius: 0;
            }
            button {
                border-radius: 0px;
                @extend .analytic_c2-bg;
                color: $white;
                &:hover {
                    @extend .analytic_c2-bg;
                    @extend .analytic_c1;
                }
            }
        }
    }
    &.email-banner {
        display: inline-flex;
        margin-bottom: 12px;
        @include mobileSm {
            flex-direction: column;
        }
        input {
            flex: 1;
            height: 58px;
            border: 1px solid #BFBFBF;
            @extend .text-color;
            background: transparent;
            padding-right: 25px;
            border-radius: 0;
            font-size: 16px;
            font-weight: $regular;
            @include mobileMd {
                padding-left: 10px;
                padding-right: 10px;
            }
            @include mobileSm {
                margin-bottom: 10px;
                padding: 20px 10px;
            }
        }
        button {
            position: static;
            padding: 20px 35px;
            margin-left: 20px;
            line-height: 1;
            border-radius: 0px;
            font-weight: $semi-bold;
            @extend .trans3;
            @extend .email_c1-bg;
            color: $white;
            &:hover {
                @extend .email_c2-bg;
                color: $white;
            }
            @include mobileSm {
                margin: 0px;
            }
        }
    }
    &.app-banner {
        @extend .box-shadow;
        border-radius: 50px;
        input {
            height: 58px;
            @extend .text-color;
            background: $white;
            padding-right: 180px;
            font-size: 16px;
            font-weight: $regular;
            @include mobileSm {
                padding-left: 10px;
                padding-right: 120px;
