/* Others */
.ov-hidden {
    overflow: hidden;
}
.ovx-hidden {
    overflow-x: hidden;
}

/* Before After */
.ba {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    @extend .c2-bg;
}

/* Pagination*/
.pagination {
    > ul {
        margin-bottom: 0;
    }
    li {
        margin: 0 5px;
        a,
        span {
            width: 40px;
            height: 40px;
            line-height: 1;
            position: relative;
            display: inline-flex;
            justify-content: center;
            align-items: center;
            background-color: $white;
            border: 1px solid #eeeeee;
            color: #5E5E5E;
            font-size: 22px;
            @extend .hind;
            font-weight: $semi-bold;
            &:hover,
            &.current {
                color: $white;
                @extend .email_c2-bg;
            }
        }
        &.nav-btn {
            a.disabled {
                cursor: not-allowed;
                opacity: .5;
            }
        }
    }
}

/* Blockquote */
blockquote {
    padding: 0px 20px 30px 20px;
    position: relative;
    font-size: 18px;
    font-style: italic;
    line-height: 1.33;
    font-weight: $regular;
    max-width: 540px;
    margin: 0 auto;
    cite {
        display: block;
        margin-top: 15px;
        font-size: 16px;
        line-height: 1.44;
        font-style: normal;
        font-weight: $regular;
    }
}

/* Custom Container */
@media (min-width: 1500px) {
    .container {
        max-width: 1410px;
    }
}

/* Form Response */
.form-response {
    background-color: rgba($c1, .2);
    span {
        display: block;
        margin-top: 20px;
        margin-bottom: 30px;
        padding: 10px 15px;
    }  
}

/* Bg */
.light-bg {
    background-color: #FFFCF6;
}

/* Bg Img */
.bg-img {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

/* Search Form */
.search-form {
