/* Count Down Timer */
.countdown-wrapper {
    padding: 50px;
    padding-bottom: 44px;
    @extend .box-shadow;
    @extend .radius-20;
    background: $white;
    @include tab {
        margin-top: 30px;
    }
    @include mobileMd {
        padding: 30px 20px 26px;
    }
    .countdown-tag {
        display: inline-block;
        background-color: #C9FFF5;
        @extend .crs_c1;
        @extend .radius-50;
        padding: 4px 22px;
        font-size: 18px;
        font-weight: $medium;
        margin-bottom: 26px;

    }
}
#countdown {
    .single-countdown {
        text-align: center;
        h2 {
            font-size: 60px;
            font-weight: $semi-bold;
            color: $black;
            @include mobileMd {
