            }
        }
    }
}

/* Video Play Button */
.video-play-button {
    width: 68px;
    height: 68px;
    @extend .radius;
    @extend .crs_c2-bg;
    color: $white;
    position: absolute;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;

    &:hover {
        @extend .crs_c1-bg;
        color: $white;
        i {
            @extend .trans2;
        }
    }
    &.proto {
        @extend .proto_c2-bg;
        &:hover {
            i {
                @extend .proto_c1;
            }
        }
    }
}

/* Trapezium Video */
.trapezium-video {
    position: relative;
    z-index: 1;
    &:after {
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        background-color: $white;
        opacity: 0.3;
        content: "";
        position: absolute;
    }
}

/* Course BG */
#course {
    background: #F6F8F5;
}

.bg-right {
    position: absolute;
    left: auto;
    right: -300%;
    top: 0;
    width: calc(400% - 15px);
    height: 100%;
    @extend .book_c1-bg;
    border-radius: 150px 0 0 0;
    z-index: -1;
    @include mobileLg {
        width: 600%;
    }
}

/* Custom Checkbox */
.custom-checkbox {
    input[type=checkbox] {
        position: relative;
        top: 1px;
    }
    &.pos {
        label {
            display: block;
            position: relative;
            padding-left: 30px;
            cursor: pointer;
            user-select: none;
        }
        label input {
            position: absolute;
            opacity: 0;
            cursor: pointer;
            height: 0;
            width: 0;
        }
        .checkmark {
            position: absolute;
            top: 6px;
            left: 0;
            height: 16px;
            width: 16px;
            background-color: transparent;
            border: 1px solid $analytic_c1;
        }
        label input:checked ~ .checkmark {
            background-color: $analytic_c1;
        }
        .checkmark:after {
            content: "";
            position: absolute;
            display: none;
        }
        label input:checked ~ .checkmark:after {
            display: block;
        }
        label .checkmark:after {
            left: 5px;
            top: 1px;
            width: 5px;
            height: 10px;
            border: solid white;
            border-width: 0 2px 2px 0;
            transform: rotate(45deg);
        }
    }
}

/* App2 Page Section BG */
.app2-bg {
    background: #F5F8FF;
}

/* About Content */
.about-content {
    @include tab {
        margin-top: 60px;
    }
