                    .btn-pay.s-btn {
                        color: $white !important;
                        background: $pay_c2;
                        &:hover {
                            background: #BBAAFF;
                            color: $white;
                        }
                    }
                }
            }
        }
    }
    &.inner-page {
        background: $white;
    }
    min-height: 100px;
