                }
            }
            .main-menu .nav > li {
                
                position: relative;
                &.current-menu-parent,
                &.current-menu-item,
                &:hover {
                    > a {
                        color: $c1 !important;
                    }
                }
                > a {
                    color: $black !important;

                    &:hover {
                        color: $c1 !important;
                    }
                }
            }
        }
        .logo {
            line-height: 1;
