/* Offcanvas Overlay */
.offcanvas-overlay {
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: #000;
    opacity: 0;
    visibility: hidden;
    @extend .trans3;
    z-index: 9998;
    &.opened {
        opacity: .3;
        visibility: visible;
    }
}

/* Offcanvas Panel */
#mobile_menu {
    position: fixed;
    width: 400px;
    height: 100vh;
    left: auto;
    right: 0;
    top: 0;
    z-index: 9999;
    background-color: #fff;
    padding: 50px;
    @extend .box-shadow;
    overflow: hidden;
    overflow-y: auto;
    @extend .trans3;
    transform: translateX(100%);
    @include mobileMd {
        width: 100%;
    }

    &.opened {
        transform: translateX(0);
    }

    .offcanvas-header {
        margin-bottom: 50px;

        .offcanvas-close {
            cursor: pointer;
        }
    }

    .mobile-main-menu {
        .nav {
            li {
                display: block;
                &.current-menu-parent,
                &.current-menu-item {
                    > a {
                        @extend .c1;
                    }

                    span.sub-menu {
                        a {
                            color: $black;
                        }
                    }
                }

                a { 
                    display: block;
                    color: #81724D;
                    font-size: 20px;
                    padding: 8px 0px;
                    text-transform: capitalize;
                    &:hover {
                        @extend .c1;
                    }
                }

                .sub-menu {
                    padding-left: 20px;
                    margin-top: 10px;

                    a {
                        font-size: 18px;
                    }
                }
            }

            span.submenu-button {
                width: 48px;
                height: 48px;
                position: absolute;
                right: 30px;
                cursor: pointer;

                &:after {
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);
                    @include fa('\f0da');
                }

                &.sub-menu-oppened {
                    &:after {
                        @include fa('\f0d7');
                    }
                }
            }

            .sub-menu {
                display: none;

                &.sub-menu-oppened {
                    display: block;
                }
            }
        }
        &.style--two {
            .nav {
                li {
                    &.current-menu-parent,
                    &.current-menu-item {
                        > a {
                            @extend .crm_c1;
                        }
