/* Menu Button */
#menu-button {
    height: 25px;
    float: right;
    z-index: 99999;
    position: relative;
    top: 12px;
    cursor: pointer;
    span {
        position: relative;
        &:before,
        &:after {
            position: absolute;
            content: '';
        }
        &,
        &:before,
        &:after {
            display: block;
            width: 25px;
            height: 2px;
            @extend .text-color-bg;
            @extend .trans3;
        }
        &::before {
            top: -7px;
        }
        &::after {
            top:  7px;
        }
    }
}

